(() => {

  'use strict';

  const angular = window.angular;

  class mainContainer {

    constructor() {
      this.restrict = 'E';
      this.templateUrl = 'packages/main-container/main-container.html';
    }

    static directiveFactory() {
      mainContainer.instance = new mainContainer();
      return mainContainer.instance;
    }

  }

  angular.module('app')
    .directive('mainContainer', [
      mainContainer.directiveFactory
    ]);

})();
